import "./styles.css";

if (document.readyState !== "loading") {
  console.log("Document is ready!");
  InitializeCode();
} else {
  document.addEventListener("DOMContentloaded", function () {
    console.log("Document is ready after loading!");
    InitializeCode();
  });
}

function InitializeCode() {
  const addCommentButton = document.getElementById("add-comment");
  let CommentCount = 0;
  addCommentButton.addEventListener("click", function () {
    var text = document.getElementById("text-area").value;
    const commentList = document.getElementById("comment-list");
    let newComment = document.createElement("li");
    newComment.innerHTML = text;
    commentList.appendChild(newComment);
    CommentCount++;
  });

  const removeCommentButton = document.getElementById("remove-comments");
  removeCommentButton.addEventListener("click", function () {
    const commentList = document.getElementById("comment-list");
    var conf = confirm("Are you sure you want to delete all comments?");
    if (conf === true) {
      while (CommentCount > 0) {
        commentList.removeChild(commentList.lastChild);
        CommentCount--;
      }
    } else {
    }
  });
}
